const { pristineContactValidator } = require('./pristineValidator');

const { getSellOrSoftwareValue } = require('./getSellOrSoftwareValue');

const { showLoader, hideLoader } = require('../loader');
const { sendEmail } = require('./sendEmail');

export function sendContactForm(submitEvent) {
  const form = document.getElementById('contact-form');
  submitEvent.preventDefault();
  showLoader();

  if (form !== null) {
    const sell_expert_or_software_expert = getSellOrSoftwareValue(
      document.getElementsByName('sell-or-software')
    );
    const from_name = document.getElementById('name').value.trim();
    const from_email = document.getElementById('email').value.trim();
    const how_can_i_help_you = document
      .getElementById('description')
      .value.trim();

    const isValidContactForm = pristineContactValidator.validate();

    if (!isValidContactForm) {
      hideLoader();

      return;
    }

    sendEmail('service_coconutt', 'template_amy02qt', {
      sell_expert_or_software_expert,
      from_name,
      from_email,
      how_can_i_help_you,
    })
      .then((response) => {
        console.log('success');
        form.reset();
      })
      .catch((error) => {
        console.error('fail');
      })
      .finally(() => {
        hideLoader();
      });
  } else {
    hideLoader();
  }
}
