const { sendContactForm } = require('./form/sendContactForm');
const { sendBudgetForm } = require('./form/sendBudgetForm');
const { initButtonActions } = require('./button-actions');
const { applyCarouselSlider } = require('./carousel');
const { initMenu } = require('./menu-action');

require('../style/main.scss');

const carouselSlider = document.querySelector('.glider');
const contactForm = document.getElementById('contact-form');
const budgetForm = document.getElementById('budget-form');

initMenu();
initButtonActions();

carouselSlider && applyCarouselSlider(carouselSlider);

contactForm && contactForm.addEventListener('submit', sendContactForm);
budgetForm && budgetForm.addEventListener('submit', sendBudgetForm);
