const { pristineBudgetValidator } = require('./pristineValidator');

const { showLoader, hideLoader } = require('../loader');
const { sendEmail } = require('./sendEmail');

export function sendBudgetForm(submitEvent) {
  const form = document.getElementById('budget-form');
  submitEvent.preventDefault();
  showLoader();

  if (form !== null) {
    const from_name = document.getElementById('company-name').value.trim();
    const from_email = document.getElementById('company-email').value.trim();
    const website = document.getElementById('website').value.trim();
    const company_challenge = document.getElementById('challenge').value.trim();
    const projectdescription = document.getElementById('project').value.trim();
    const quote_from = document.getElementById('budget-from').value.trim();
    const quote_to = document.getElementById('budget-to').value.trim();

    const isValidBudgetForm = pristineBudgetValidator.validate();
    console.log(isValidBudgetForm);

    if (!isValidBudgetForm) {
      hideLoader();

      return;
    }

    sendEmail('service_coconutt', 'template_sokw4oe', {
      from_name,
      from_email,
      website,
      company_challenge,
      projectdescription,
      quote_from,
      quote_to,
    })
      .then((response) => {
        console.log('success');
        form.reset();
      })
      .catch((error) => {
        console.error('fail');
      })
      .finally(() => {
        hideLoader();
      });
  } else {
    hideLoader();
  }
}
